/**
 * shell
 * `sed -i "s/<gitcount>/$(git rev-list HEAD --count)/g" ./src/common/version.ts`
 * git <gitcount> 
 * `sed -i "s/<gitbranch>/$(git symbolic-ref --short -q HEAD)/g" ./src/common/version.ts`
 * git <gitbranch> 
 */

const version = '<gitbranch>/1.2.0.<gitcount>'

export default version
