import Event from 'events'
import { TIMEOUT } from '../../common/constant'
import {
  Connect,
  ConnectAck,
  Disconnect,
  DisconnectAck,
  Bind,
  BindAck,
  Unbind,
  UnbindAck,
  Push,
  PushAck,
  Call,
  CallAck,
} from '../../common/types/rtmq'
import cloneDeep from 'lodash/cloneDeep'

type RtmqData = Connect | Disconnect | Bind | Unbind | Push | Call | ConnectAck | DisconnectAck | BindAck | UnbindAck | PushAck | CallAck

class AckRequest {
  _event: Event;
  _timeoutTimer: number;

  constructor(request, rtmqData: RtmqData) {
    this._event = new Event()
    request && request()

    this._timeoutTimer = window.setTimeout(() => {
      this.emit('timeout', rtmqData)
    }, TIMEOUT * 1000) // TODO:

    this._responseHandle()
  }

  _responseHandle() {
    const self = this
    const handles = ['success', 'fail', 'timeout']
    handles.map((eventType) => {
      this._event.on(eventType, function () {
        clearTimeout(self._timeoutTimer)
      })
    })
  }

  /**
   * 
   * @param {function} cb 
   */
  then(cb) {
    this._event.on('success', function (data: RtmqData) {
      const cloneData: RtmqData = cloneDeep(data)
      cb && cb(cloneData)
    })
    return this
  }


  /**
   * 
   * @param {function} cb 
   */
  onFail(cb) {
    this._event.on('fail', function (data: RtmqData) {
      const cloneData: RtmqData = cloneDeep(data)
      cb && cb(cloneData)
    })
    return this
  }

  /**
   * 
   * @param {function} cb 
   */
  onTimeout(cb) {
    cb && this._event.on('timeout', cb)
    return this
  }

  /**
   * 
   * @param {stirng} type 
   * @param {object} data 
   */
  emit(type, data) {
    this._event.emit(type, data)
  }
}
export default AckRequest
