export enum CMD_CODE {
  CONNECT = 1,
  CONNECTACK = 2,
  DISCONNECT = 3,
  DISCONNECTACK = 4,
  UPDATE = 5,  //  
  UPDATEACK = 6,
  BIND = 7, //  
  BINDACK = 8,  //
  UNBIND = 9, //  login out
  UNBINDACK = 10,
  PING = 11, //   setTimeout 2000
  PONG = 12, //   * 2
  PUSH = 13, //  
  PUSHACK = 14,
  CALL = 15,
  CALLACK = 16,
}

export enum PLATFORM_TYPE
{
  Unknow = 0,
  Win = 1,
  Android = 2,
  iOS = 3,
  Web = 4,
  SeewoOS = 5,
  Mac = 6,
  Linux = 7,
}


export interface RtmqPack {
  cmd: CMD_CODE;// 
  seq: number; // 
  data: number; // 
  extend?: number; // 
}

// 
export interface Connect
{
  traceid: string; // ID,UUID
  appid: string; // ID
  cid: string; // ID
  uid: string; // ID
  pwd: string; // 
  data: ArrayBuffer; // 
  will: ArrayBuffer; // 
  signsure: string; // 
  version: string; // 
  platform: PLATFORM_TYPE; // 
  heartbeat: number; // 
}

export interface ConnectAck
{
  traceid: string;  // ID
  appid: string;  // ID
  cid: string;  // ID
  uid: string;  // ID
  token: string;  // webtoken
  data: ArrayBuffer;  // 
  code: number;  // 0
  message: string;  // 
}


